  async componentDidMount  () {
    var chartGender = chartDoughnutSample;
    chartGender.datasets[0].data.push(await this.getMalePercent());
    chartGender.labels.push("Nữ");
    chartGender.datasets[0].data.push(await this.getFeMalePercent());
    chartGender.labels.push("Nam");
    
    var chartType = chartDoughnutSample;
    chartType.datasets[0].data.push(await this.getDuKhachPercent());
    chartType.labels.push("Du khách");
    chartType.datasets[0].data.push(await this.getKhachVietPercent());
    chartType.labels.push("Khách Việt");
    this.setState({
      chartGender: chartGender,
      chartType: chartType
    })
    console.log(chartGender);
    console.log(chartType);
    console.log(chartDoughnutModel);
    
    
  }

  async getDuKhachPercent() {
    const response = await axios({
      url: 'https://tdh-survey-tool.herokuapp.com/survey/filterTypePercent?type=Du khách',
      method: 'get'
    });
    return response.data.result;    
  }
  async getKhachVietPercent() {
    const response = await axios({
      url: 'https://tdh-survey-tool.herokuapp.com/survey/filterTypePercent?type=Khách Việt',
      method: 'get'
    });
    return response.data.result;    
  }
  async getMalePercent() {
    const response = await axios({
      url: 'https://tdh-survey-tool.herokuapp.com/survey/filterGenderPercent?type=male',
      method: 'get'
    });
    return response.data.result;    
  }

  async getFeMalePercent() {
    const response = await axios({
      url: 'https://tdh-survey-tool.herokuapp.com/survey/filterGenderPercent?type=female',
      method: 'get'
    });
    return response.data.result;
  }

