import React, { Component } from 'react';
// import 'bootstrap/dist/css/bootstrap.min.css';
import pages from './pages'
import axios from 'axios';

import * as Survey from "survey-react";
import "survey-react/survey.css";
import "./custom/custom-survey.css";
class App extends Component {
  //Define Survey JSON
  //Here is the simplest Survey with one text question


  json = {
    title: "THUY DESIGN HOUSE",
    pages: pages,
    completedHtml: "Hoàn tất",
    

  }
  //Define a callback methods on survey complete
  onComplete(survey, options) {
    //Write survey results into database
    console.log("Survey results: " + JSON.stringify(survey.data));

  }



  model = new Survey.Model(this.json);

  render() {


Survey
    .StylesManager
    .applyTheme("default");

    this.model.pageNextText = "Đi tiếp";
    this.model.pagePrevText  = "Quay về trước";
    this.model.completeText = "Hoàn tất";
    this.model.showProgressBar = 'bottom';
    // this.model.goNextPageAutomatic = true; 
    this.model
    .onUpdateQuestionCssClasses
    .add(function (survey, options) {
        var classes = options.cssClasses
        classes.sv_header = "sv_header";
        classes.sv_page_title= "sv_page_title";
        classes.sv_q_title= "sv_q_title";
        classes.sv_q_imgsel= "sv_q_imgsel"; 
        classes.sv_completed_page = "sv_completed_page";
        
        // classes.root = "sq-root";
        // classes.title = "sq-title"

        // classes.label = "sq-label";

        // if (options.question.isRequired) {
        //     classes.title += " sq-title-required";
        //     classes.root += " sq-root-required";
        // }

        // if (options.question.getType() === "checkbox") {
        //     classes.root += " sq-root-cb";
        // }
    });



    this.model
      .onComplete
      .add(function (sender, options) {
        //Show message about "Saving..." the results
        options.showDataSaving(); //you may pass a text parameter to show your own text
        // axios.post('https://tdh-survey-tool.herokuapp.com/survey', sender.data)
        axios.post('localhost:5000/survey', sender.data)

          .then((res) => {
            console.log(res);
          })
          .catch((err) => {
            console.log(err);
          })
      });



    return (<Survey.Survey model={this.model} onComplete={this.onComplete} />);

  }

}



export default App;
